import { service } from '@/request/index'
// 轮播图
const getBanner = () => {
  return service({
    method: 'get',
    url: 'banner?type=2'
  })
}
// 歌单列表
const getMusicList = () => {
  return service({
    method: 'get',
    url: '/personalized?limit=10'
  })
}
// 搜索
const getSearchMusic = key => {
  return service({
    method: 'get',
    url: `/search?keywords=${key}`
  })
}
export { getBanner, getMusicList, getSearchMusic }
