import { reactive, onMounted, defineComponent } from 'vue';
import { getBanner } from '@/api/home/index';
export default defineComponent({
  setup() {
    // 定义数据
    const state = reactive({
      images: []
    });
    // 使用函数
    onMounted(async () => {
      let res = await getBanner();
      state.images = res.data.banners;
    });
    return {
      state
    };
  }
});