import "core-js/modules/es.array.unshift.js";
import { getSearchMusic } from '@/api/home/index';
import { defineComponent } from 'vue';
export default defineComponent({
  data() {
    return {
      keyWordList: [],
      searchKey: '',
      searchList: []
    };
  },
  mounted() {
    if (localStorage.getItem('keyWordList')) this.keyWordList = JSON.parse(localStorage.getItem('keyWordList'));else this.keyWordList = [];
  },
  methods: {
    keyWord: async function () {
      if (this.searchKey.trim() !== '') {
        this.keyWordList.unshift(this.searchKey);
        this.keyWordList = [...new Set(this.keyWordList)];
        localStorage.setItem('keyWordList', JSON.stringify(this.keyWordList));
        if (this.keyWordList.length > 3) {
          this.keyWordList.splice(this.keyWordList.length - 1, 1);
        }
        let res = await getSearchMusic(this.searchKey);
        this.searchList = res.data.result.songs;
      }
      this.searchKey = '';
    },
    deleteLocal: function () {
      if (this.keyWordList) {
        localStorage.removeItem('keyWordList');
        this.keyWordList = [];
      }
    },
    searchHistory: async function (item) {
      let res = await getSearchMusic(item);
      this.searchList = res.data.result.songs;
    },
    updateIndex: function (item) {
      item.al = item.album;
      item.al.picUrl = item.album.artist.img1v1Url;
      this.$store.commit('pushPlayList', item);
      this.$store.commit('updatePlayListIndex', this.$store.state.playList.length - 1);
    }
  }
});