import { defineComponent, onMounted, reactive } from 'vue';
import { Vue3Marquee } from 'vue3-marquee';
import 'vue3-marquee/dist/style.css';
import { mapMutations, mapState } from 'vuex';
export default defineComponent({
  // 模块注册
  components: {
    Vue3Marquee
  },
  // 定义属性
  props: ['musicList', 'btnPause', 'play', 'addDuration'],
  setup(props) {
    onMounted(() => {
      props.addDuration();
    });
    const state = reactive({
      isLyricShow: false
    });
    return {
      state,
      onMounted
    };
  },
  methods: {
    ...mapMutations(['updateDetailShow', 'updatePlayListIndex']),
    backHome: function () {
      this.updateDetailShow();
      this.state.isLyricShow = false;
    },
    goPlay: function (num) {
      let index = this.playListIndex + num;
      if (index < 0) {
        index = this.playList.length - 1;
      } else if (index === this.playList.length) {
        index = 0;
      }
      this.updatePlayListIndex(index);
    }
  },
  computed: {
    ...mapState(['lyricList', 'currentTime', 'playListIndex', 'playList', 'duration']),
    lyric: function () {
      let arr = new Array();
      if (this.lyricList.lyric) {
        arr = this.lyricList.lyric.split(/[(\r\n)\r\n]+/).map((item, i) => {
          let min = item.slice(1, 3);
          let sec = item.slice(4, 6);
          let mill = item.slice(7, 10);
          let lyric = item.slice(11, item.length);
          if (isNaN(Number(mill))) {
            mill = item.slice(7, 9);
            lyric = item.slice(10, item.length);
          }
          if (isNaN(min)) {
            min = '';
            sec = '';
            mill = '';
            lyric = item.slice(0);
          }
          let time = parseInt(min) * 60 * 1000 + parseInt(sec) * 1000 + parseInt(mill);
          return {
            min,
            sec,
            mill,
            lyric,
            time
          };
        });
        arr.forEach((item, i) => {
          if (i === arr.length - 1 || isNaN(arr[i + 1].time)) {
            item.pre = 100000;
          } else {
            item.pre = arr[i + 1].time;
          }
        });
      }
      return arr;
    }
  },
  watch: {
    currentTime: function (val) {
      let p = document.querySelector('.p_active');
      if (p) if (p.offsetTop > 300) {
        this.$refs.lyrics.scrollTop = p.offsetTop - 300;
      }
      if (val === this.duration) {
        this.goPlay(1);
      }
    }
  }
});