import { service } from '@/request/index'

const getMusicItemList = (id) => {
  return service({
    method: 'get',
    url: `/playlist/detail?id=${id}`
  })
}

const getItemList = (id) => {
  return service({
    method: 'get',
    url: `/playlist/track/all?id=${id}&limit=10&offset=0`
  })
}

const getMusicLyric = (id) => {
  return service({
    method: 'get',
    url: `/lyric?id=${id}`
  })
}

export { getMusicItemList, getItemList, getMusicLyric }
