import { reactive, onMounted, computed, defineComponent } from 'vue';
import { getMusicList } from '@/api/home/index';
export default defineComponent({
  setup() {
    // 定义数据
    const state = reactive({
      musicList: []
    });
    // 定义函数
    const changeMount = computed(() => {
      return num => {
        let newNum = num;
        if (num >= 100000000) {
          newNum = (num / 100000000).toFixed(1) + '亿';
        } else if (num >= 10000) {
          newNum = (num / 10000).toFixed(1) + '万';
        }
        return newNum;
      };
    });
    // 使用函数
    onMounted(async () => {
      let res = await getMusicList();
      state.musicList = res.data.result;
    });
    return {
      state,
      changeMount
    };
  }
});