import { createStore } from 'vuex'
import { getMusicLyric } from '@/api/item'

export default createStore({
  state: {
    playList: [
      {
        al: {
          id: 135992033,
          pic: 109951166609630670,
          picUrl: 'https://p1.music.126.net/m8BMzRWR53lMu2uaMYV2mA==/109951166609630672.jpg',
          pic_str: '109951166609630672'
        },
        id: 1894094482,
        name: '漠河舞厅·2022'
      }
    ],
    playListIndex: 0,
    btnPause: true,
    detailShow: false,
    lyricList: {},
    currentTime: 0,
    duration: 0,
    token: ''
  },
  mutations: {
    changeBtn: function (state, value) {
      state.btnPause = value
    },
    pushPlayList: function (state, val) {
      state.playList.push(val)
    },
    updatePlayList: function (state, value) {
      state.playList = value
    },
    updatePlayListIndex: function (state, value) {
      state.playListIndex = value
    },
    updateDetailShow: function (state) {
      state.detailShow = !state.detailShow
    },
    updateLyricList: function (state, val) {
      state.lyricList = val
    },
    updateCurrentTime: function (state, val) {
      state.currentTime = val
    },
    updateDuration: function (state, val) {
      state.duration = val
    },
    uodateToken: function (state, val) {
      state.token = val
      localStorage.setItem('token', state.token)
    }
  },
  actions: {
    getLyric: async function (context, value) {
      let res = await getMusicLyric(value)
      context.commit('updateLyricList', res.data.lrc)
    }
  }
})
